"use client"

import { useState, useEffect } from "react"
import { X } from "lucide-react"

export default function VideoParallax() {
  const [open, setOpen] = useState(false)

  // Disable scroll when modal is open
  useEffect(() => {
    document.body.style.overflow = open ? "hidden" : "auto"
  }, [open])

  return (
    <>
      {/* PARALLAX SECTION */}
      <section className="relative overflow-hidden bg-[#1A1A1A] py-40">

        {/* Curved mask */}
        <div className="absolute inset-0 rounded-[80px] md:rounded-[150px] overflow-hidden mx-auto max-w-[1500px] border-[20px] border-[#1A1A1A]/80">

          {/* Background image with parallax motion */}
          <div
            className="absolute inset-0 bg-cover bg-center scale-110 animate-[parallax_15s_linear_infinite]"
            style={{ backgroundImage: "url('/video_parralax.jpg')" }}
          />

          {/* Dark overlay */}
          <div className="absolute inset-0 bg-black/40" />
        </div>

        {/* CONTENT (Play button + text) */}
        <div className="relative flex flex-col items-center justify-center">
          <button
            onClick={() => setOpen(true)}
            className="group relative z-10"
          >
            {/* Outer hover ring */}
            <div className="absolute inset-0 rounded-full border border-white/40 scale-125 opacity-0 group-hover:opacity-100 group-hover:scale-150 transition-all duration-500"></div>

            {/* Circle button */}
            <div className="w-28 h-28 md:w-32 md:h-32 bg-[#E8DDD3] rounded-full flex items-center justify-center shadow-xl group-hover:scale-110 transition-all duration-300">
              {/* Play icon */}
              <svg
                width="36"
                height="36"
                viewBox="0 0 24 24"
                fill="black"
              >
                <path d="M8 5v14l11-7z" />
              </svg>
            </div>
          </button>

          <p className="text-white text-lg mt-6 tracking-wide">VIDEO TOUR</p>
        </div>

      </section>

      {/* VIDEO MODAL */}
      {open && (
        <div className="fixed inset-0 bg-black/80 backdrop-blur-sm z-50 flex items-center justify-center px-4">
          <div className="relative w-full max-w-5xl">
            {/* Close button */}
            <button
              className="absolute top-4 right-4 text-white hover:text-gray-300 transition z-50"
              onClick={() => setOpen(false)}
            >
              <X size={32} />
            </button>

            {/* Video */}
            <div className="bg-black rounded-xl overflow-hidden shadow-2xl">
              <video
                src="/video.mp4"
                controls
                autoPlay
                className="w-full"
              />
            </div>
          </div>
        </div>
      )}
    </>
  )
}
